@images-url: "../images/";

@import "bootstrap/bootstrap";

@logo-width: 250px;

.container-fluid {
	max-width: @screen-fullhd;
	margin-left: auto;
	margin-right: auto;
}

body {
	background: url("@{images-url}bg.png");
}

.navbar {
	margin-top: 66px;
	padding-top: 50px;
}

.navbar-logo {
	position: absolute;
	top: 0;
	left: 50%;
	.translate(-50%; 0);
}

.navbar-nav {
	width: 50%;

	.lang {
		padding: 8px;
		
		>a {
			color: @black !important;
			background: @sarga;
			padding: 7px;
		}
	}
}

.navbar-left {
	padding-right: @logo-width / 2 + 35px;
	text-align: right;

	> li {
		float: right;
	}
}

.navbar-right {
	padding-left: @logo-width / 2 + 35px;
}

.kocka {
	max-height: 880px;
	height: 45.8vw;
	overflow: hidden;
	.clearfix();
	background: @keret-felso no-repeat 50% 0,
				@keret-also no-repeat 50% 100%,
				@keret-bal no-repeat 0 50%,
				@keret-jobb no-repeat 100% 50%;
}

header {
	background-color: black;
}